.App {
  text-align: center;
}

.App-header {
  background-color: palevioletred;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

header {
	background-color: aquamarine;
	color: plum;
}

h1 {
	text-align: center;
}

div img {
	background-position: 0px;
	width: auto;
	height: 570px;
	border-radius: 25px;
	display: flex;
	justify-content: stretch;
	align-items: center;
} 


